.message {
    margin: 0 16px;
    padding: 0 16px;

    height: 50px;
    border-radius: 8px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    opacity: 1;
    visibility: visible;
    transition: .3s 0s;

    b {
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
    }

    svg {
        cursor: pointer;
    }

    &.hide {
        opacity: 0;
        visibility: hidden;
    }

    &.error {
        background: rgba(#D83B58, .1);

        svg {
            fill: #DC544C;
        }

        b {
            color: #DC544C;
        }
    }
}