@import "../utils/variable";
.login {
    height: 100%;

    &__header {
        display: flex;
        justify-content: center;
        padding: 32px 0;

        background: $dark;
    }

    &__title {
        font-weight: 500;
        font-size: 24px;
        line-height: 24px;

        text-align: center;
        color: $black;

        padding: 32px 0 16px;
    }

    &__supTitle {
        text-align: center;
        font-size: 16px;
        line-height: 18px;
    }

    &__input {
        $input: &;
        position: relative;

        &-body {
            width: 100%;
            box-sizing: border-box;

            height: 44px;

            border: 1px solid #ECECEC;
            border-radius: 4px;
            outline: none;

            color: $black;
            font-size: 14px;
            line-height: 16px;

            padding: 0 0 0 48px;
            transition: .2s 0s;

            &:focus {
                border: 1px solid #A09039;
            }

            &::placeholder {
                color: $gray;
            }
        }

        &-error {
            font-size: 12px;
            line-height: 14px;
            color: #DC544C;

            position: absolute;
            top: calc(100% + 3px);
        }

        &-eye,
        &-icon {
            position: absolute;

            top: 50%;
            left: 14px;

            transform: translateY(-50%);
        }

        &-eye {
            left: auto;
            right: 10px;

            cursor: pointer;
        }

        &-submit {
            width: 100%;
            height: 50px;

            margin: 32px 0;

            background: $green;
            border-radius: 8px;
            transition: .3s 0s;

            font-weight: 500;
            font-size: 14px;
            line-height: 18px;
            letter-spacing: 0.04em;
            color: $white;
            cursor: pointer;

            border: none;

            &.disabled {
                cursor: not-allowed;
                background: $gray1;
            }
        }

        & + & {
            margin-top: 24px;
        }

        &-wrapper {
            margin: 32px 16px 0;
        }

        &--invalid {

            #{$input} {

                &-body {
                    border-color: #DC544C;
                }
            }
        }
    }
}