.loader {
    display: flex;
    flex-direction: column;

    height: 100%;

    align-items: center;
    justify-content: center;

    opacity: 0;
    visibility: hidden;
    transition: .3s 0s;

    &__helpers {
        margin-top: 32px;
        display: flex;

        li {
            width: 8px;
            height: 8px;
            border-radius: 4px;
            margin: 0 4px;

            &:nth-child(odd) {
                background: #151515;

                animation: circle 2s infinite;
                animation-delay: 0s;
            }

            &:nth-child(even) {
                background: #A09039;
                animation: circle 2s infinite;
                animation-delay: 3s;
            }
        }
    }

    &.isActive {
        opacity: 1;
        visibility: visible;
    }
}

@keyframes circle {
    0% {
        opacity: 1;
    }
    50% {
        opacity: .5;
    }
    100% {
        opacity: 1;
    }
}